import request from '@/utils/request.js';

export const getGameList = (page = 1, size = 10, name) => {
  return request.get(`gameInfo?page=${page}&size=${size}&gameName=${name}`)
}


export const removeGameById = (id) => {
  return request.delete(`gameInfo?id=${id}`)
}

export const saveGame = (gameName) => {

  return request.post('gameInfo', {gameName})
}
