<script>
export default {
  name: "GameList"
}
</script>
<script setup>
import {onMounted, reactive, ref} from 'vue'
import {getGameList, removeGameById, saveGame} from "../../api/game";

const dialogVisible = ref(false)
import {ElMessage, ElMessageBox} from "element-plus";

const gameName = ref('')
const formInline = reactive({
  name: '',
  page: 1,
  size: 10
})
const tableData = ref([])

const dataLoad = async () => {
  const data = await getGameList(formInline.page, formInline.size, formInline.name)
  tableData.value = data.data.records
}
onMounted(() => {

  dataLoad()
})
const onSubmit = () => {
  dataLoad()
}

const removeGame = async (id) => {
  const res = await ElMessageBox.confirm(
    '?',
    'Danger',
    {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }
  )

  if (res) {
    const data = await removeGameById(id)
    if (data.code === 200) {
      ElMessage.success('')
      await dataLoad()
    } else {
      ElMessage.error('')
    }
  }


}


const addGame = async () => {
  if (!gameName.value) {
    ElMessage.error('')
    return
  }
  saveGame(gameName.value).then(res => {
    if (res.code === 200) {
      ElMessage.success('')
      dialogVisible.value = false
      gameName.value = ''
      dataLoad()
    } else {
      ElMessage.error('')
    }
  })
}
</script>

<template>

  <el-form :inline="true" :model="formInline" class="demo-form-inline">
    <el-form-item label="">
      <el-input v-model="formInline.name" placeholder=""/>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="onSubmit"></el-button>
      <el-button type="primary" @click="dialogVisible=true"></el-button>
    </el-form-item>
  </el-form>

  <el-table :data="tableData" style="width: 100%">
    <el-table-column prop="gameName" label="" width="180"/>
    <el-table-column prop="createTime" label="" width="180"/>
    <el-table-column fixed="right" label="Operations" width="120">
      <template #default="scope">
        <el-button link type="danger" size="small" @click.prevent="removeGame(scope.row.gameId)">
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>


  <el-dialog
    v-model="dialogVisible"
    title="Tips"
    width="30%"
  >

    <div class="game-form">

      <el-input v-model="gameName"></el-input>

      <el-button type="primary" @click="addGame"></el-button>
    </div>
  </el-dialog>
</template>


<style scoped lang="less">

.game-form {
  display: flex;
  align-content: center;
  justify-content: center;
}
</style>
